.DataShippingHero{height:311px;box-shadow:0px 4px 4px rgba(0,0,0,.25)}.data-shipping-title{font-size:3.15rem;font-weight:700;display:flex;justify-content:start;padding-bottom:9.5rem;padding-left:7.2rem}.data-shipping-request-container{width:67%;margin:3.3rem auto 10rem auto;padding:5.5rem 3.5rem;background-color:#fff;box-shadow:0 8px 32px 0 rgba(126,126,126,.37);border:1px solid rgba(126,126,126,.37);border:1px solid #f1f1f1;border-radius:.3rem}.data-shipping-request-flex-container{display:flex;gap:1rem}.data-shipping-request-text{color:#333;font-weight:300;font-size:.97rem;margin-right:1rem}.data-shipping-request-text.inactive{opacity:.3}.data-shipping-request-text.active{opacity:1}.data-shipping-request-steps{font-weight:500}.data-dotted-border{border-top:1px dotted #d3d3d3;margin:.9rem 0}.data-shipping-request-des{color:#333;line-height:1.8rem;font-size:1rem;font-weight:400;padding-bottom:.3rem}.data-shipping-request-des-select{color:#333;font-weight:600;background-color:#fff}.data-shipping-request-des-select-two{color:#333;font-weight:600;padding-top:1.5rem;background-color:#fff}.data-shipping-request-select{width:50%;padding:.5rem;border:1px solid #d1d5db;border-radius:.3rem;background-color:#fafafa}.data-shipping-request-button{display:flex;background-color:#2b6cb0;border:#2b6cb0;color:#fff;padding:.5rem 1.5rem;border-radius:.3rem;transition:ease-in-out .3s}.data-shipping-request-button:hover{background-color:#1b436e}.checkbox-group{display:flex;flex-direction:column;gap:.2rem;margin-bottom:1rem}.data-shipping-request-checkbox{line-height:1.8rem;font-weight:300;font-size:.9rem}.data-shipping-request-checkbox.bold{font-weight:700}input[type=checkbox]{appearance:none;background-color:#fafafa;border:1px solid #d2d2d2;border-radius:3px;display:inline-block;position:relative;width:.8rem;height:.8rem;margin-right:.3rem}input[type=checkbox]:checked::before{content:"✓";display:block;text-align:center;color:#000;font-weight:bold;font-size:.7rem;line-height:.8rem}.data-shipping-request-button-group{display:flex}.data-shipping-request-button-group :last-child{margin-left:.5rem}.data-shipping-request-textarea{width:100%;height:155px !important;padding:10px;border:1px solid #ebebeb;border-radius:4px;background-color:#fafafa;resize:none;outline:none}.data-shipping-request-des-info{color:#333;font-size:.8rem;font-weight:400;padding-top:1.3rem}.data-shipping-request-info{margin-bottom:1rem}.data-shipping-request-label{color:#333;font-weight:700;margin-bottom:.5rem}.data-shipping-request-label-title{color:#333;font-weight:700;padding-top:.5rem;font-size:1.2rem;line-height:.8rem}.data-shipping-request-name{display:flex;gap:1rem}.data-shipping-request-info-text{width:100%;padding:.5rem;border:1px solid #d1d5db;border-radius:.375rem;background-color:#fafafa;outline:none}.data-shipping-request-info-text-half{width:49%;padding:.5rem;margin-top:1rem;border:1px solid #d1d5db;border-radius:.375rem;background-color:#fafafa;outline:none}.data-shipping-request-text-description{margin-bottom:3rem;color:#333;font-size:.8rem;font-weight:400;line-height:1.8rem}input[type=text],input[type=email],input[type=tel]{background-color:#fafafa;color:#000}