ace-between;margin-bottom:1rem}.remote-IT-annual-plan-review-group{display:flex;align-items:center}.remote-IT-annual-plan-review-stars{color:#f59e0b;font-size:1.2rem}.remote-IT-annual-plan-review-rating{color:#333;font-size:.9rem}.remote-IT-annual-plan-review-image{display:flex;align-items:center;justify-content:center;margin-bottom:1rem}.remote-IT-annual-plan-review-img{height:4rem;width:8rem}.remote-IT-annual-plan-review-content{text-align:center;margin-bottom:1rem}.remote-IT-annual-plan-review-des{font-size:.95rem;font-weight:600;color:#666;margin:0;text-align:start}.remote-IT-annual-plan-review-list{list-style:"• ";text-align:start;color:#838586;font-size:.85rem;line-height:1.1rem}.device-protection-tech-form-section{padding:1rem 1.5rem}.device-protection-tech-form{display:flex;flex-direction:column}.device-protection-tech-payment-title{font-size:1.8rem;font-weight:600;color:#1b436e;text-align:center;margin:0 auto;margin-bottom:2rem}.device-protection-tech-form-group{margin-bottom:1rem}.device-protection-tech-label{display:block;font-size:1.03rem;font-weight:700;margin-bottom:.25rem;color:#374151}.device-protection-tech-required{color:#ef4444}.device-protection-tech-input-group{display:flex;align-items:center}.device-protection-tech-terms-text{font-size:.875rem;color:#374151;margin-top:1rem;line-height:.5}.device-protection-tech-input{margin-top:.25rem;display:block;width:100%;padding:.5rem;border:1px solid #d1d5db;border-radius:.375rem;box-shadow:0 0 0 1px #d1d5db;transition:border-color .3s ease,box-shadow .3s ease}.device-protection-tech-encryption{height:46px;width:137px;margin:1rem 0}